import { Component, AfterViewInit, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { ChangeDetectionStrategy } from '@angular/core';

@Component({
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AppComponent implements AfterViewInit, OnInit {
  @Input() mathString: string;

  @ViewChild('mathField', {static: false}) mathField: ElementRef;

  MQ = null;

  ngOnInit() {}

  ngAfterViewInit() {
    // tslint:disable-next-line: no-any -- window as any is valid for typing the js window variable
    this.MQ = (window as any).MathQuill.getInterface(2);
    this.MQ.StaticMath(this.mathField.nativeElement).latex(this.mathString);
  }
}
